import React from "react";

import "./InformationBanner.css"

import "../../home/Home.css"

import "../../shared/Shared.css"

function InformationBanner(props) {

    const image_position = props.image_position === 'left' ? 'image-left' : 'image-right';
    const image = props.image;
    const text_content = props.text_content;

    return (
        <div className={"information-banner-container " + image_position}>

            <div className="information-banner-item image">
                {image}
            </div>
        
            <div className="vertical-separator" />

            <div className="information-banner-item text">
                {text_content}
            </div>

        </div>
    )

}

export default InformationBanner;