import React, { useState } from "react";
import { NavItems } from "./NavItem";

import Logo from "../../assets/logo-simple-small.png"

import "./Navbar.css"

function Nav() {
    const [clicked, setClicked] = useState(false);

    function toggleClicked() {
        setClicked(!clicked);
    }

    return (
        <nav className="NavbarItems">
            <div className="menu-icon" onClick={toggleClicked}>
                <i className={clicked ? 'fas fa-times' : 'fas fa-bars'}></i>
            </div>
            <ul className={clicked ? 'nav-menu active' : 'nav-menu'}>
                {NavItems.map((item, index) => {
                    return (
                        (() => {
                            if (item.cName === 'nav-links') {
                                return (
                                    <li key={index}>
                                        <a className={item.cName} href={item.url}>
                                            {item.title}
                                        </a>
                                    </li>
                                )
                            } else if (item.cName === 'nav-link-separator') {
                                return (
                                    <li key={index}>
                                        <div className={item.cName} />
                                    </li>
                                )
                            } else if (item.cName === 'nav-logo') {
                                return (
                                    <li key={index}>
                                        <a href={item.url}>
                                            <img className={item.cName} src={Logo} alt='Pantry Parcels' />
                                        </a>
                                    </li>
                                )
                            }
                        })()
                    )
                })}
            </ul>
        </nav>
    )
}

export default Nav;