import React, { useRef, useState } from "react";

import axios from "axios";
import ReCAPTCHA from "react-google-recaptcha"

import Twitter from "../../assets/twitter.png"
import Facebook from "../../assets/facebook.png"
import LinkedIn from "../../assets/linkedin.png"
import Instagram from "../../assets/instagram.png"

import "./ContactForm.css"

function ContactForm() {

    const formId = "H4nJ7XLO";
    const formSparkUrl = `https://submit-form.com/${formId}`;

    const recaptchaKey = "6LcYX5kfAAAAAFAMo5069nzdR6vRfM66rd76zDX7";
    const recaptchaRef = useRef();

    const initialFormState = {
        name: ' ',
        email: '',
        message: '',
    };

    const [formState, setFormState] = useState(initialFormState);
    const [submitting, setSubmitting] = useState(false);
    const [message, setMessage] = useState();
    const [recaptchaToken, setRecaptchaToken] = useState();

    const submitForm = async (event) => {
        event.preventDefault();
        setSubmitting(true);
        await postSubmission();
        setSubmitting(false);
    }

    const postSubmission = async () => {
        const payload = {
            ...formState,
            "g-recaptcha-response": recaptchaToken,
        }
        try {
            const result = await axios.post(formSparkUrl, payload);
            console.log(result);
            setMessage("Thanks for contacting us! We will be in touch shortly.")
        } catch (error) {
            console.log(`error: ${error}`);
            setMessage("Sorry there was a problem submitting your message. Please try again later or try a different contact method.")
        }
        recaptchaRef.current.reset();
    }


    // TODO: update this to only allow submission if required fields are filled out.
    const updateFormControl = (event) => {
        const { id, value } = event.target;
        const updatedFormState = { ...formState }
        updatedFormState[id] = value;
        setFormState(updatedFormState);
    }

    const updateRecaptchaToken = (token) => {
        setRecaptchaToken(token);
    }

    return (
        <div className="contact-banner-container">

            <div className="contact-banner-text-container">
                <h1 className="contact-text header">Contact Us</h1>
                <p className="contact-text body">We would love to hear from you! <br /> If you would like to leave us a message just fill out the form, otherwise head to our contact page to find out other ways to get in touch!</p>
                <p className="contact-text body">You can also contact us via LinkedIn, Instagram, Facebook or Twitter as linked below.</p>
                <div className="contact-social-media-container">
                    <a target="_blank" rel="noreferrer" href="https://twitter.com/pantryparcels">
                        <img src={Twitter} alt="twitter logo" className="contact-social-media-icon"/>
                    </a>
                    <a target="_blank" rel="noreferrer" href="https://www.facebook.com/pantry.parcels/">
                        <img src={Facebook} alt="facebook logo" className="contact-social-media-icon"/>
                    </a>
                    <a target="_blank" rel="noreferrer" href="https://www.instagram.com/pantryparcels_york/">
                        <img src={Instagram} alt="instagram logo" className="contact-social-media-icon"/>
                    </a>
                    <a target="_blank" rel="noreferrer" href="https://www.linkedin.com/company/enactus-york/">
                        <img src={LinkedIn} alt="linkedin logo" className="contact-social-media-icon"/>
                    </a>
                </div>
            </div>

            <form className="contact-banner-form-container" onSubmit={submitForm}>

                {message && <div className="submit-message-text">{message}</div>}

                <label className="contact-form-label">Name</label>
                <input
                    id="name"
                    value={formState.name}
                    className="contact-input-field details"
                    type="text"
                    onChange={updateFormControl}
                />

                <label className="contact-form-label">Email</label>
                <input
                    id="email"
                    value={formState.email}
                    className="contact-input-field details"
                    type="text"
                    onChange={updateFormControl}
                />

                <label className="contact-form-label">Message</label>
                <textarea
                    id="message"
                    value={formState.message}
                    className="contact-input-field message"
                    type="text"
                    onChange={updateFormControl}
                />

                <ReCAPTCHA
                    ref={recaptchaRef}
                    sitekey={recaptchaKey}
                    onChange={updateRecaptchaToken}
                    className="contact-recaptcha"
                />

                <button disabled={submitting} className="contact-form-submit-button">{submitting ? "Submitting..." : "Submit"}</button>

            </form>

        </div>
    );

}

export default ContactForm;